import uniRequest from 'uni-request'
export default {
	data() {
		return {
			notices:[]
		}
	},
	onLoad(){
		this.getDate()
	},
	methods: {
		getDate(){
			uniRequest({
				url:'/announcement/list/',
				method:'get'
			}).then(res=>{
				console.log(res)
				this.notices = res.data
			}).catch(error=>{
				console.log(error)
			})
		},
		navTo(id){
			uni.navigateTo({
				url:'/pages/notice/noticeDetail?id='+id
			})
		}
	}
}
